/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import {
  selectListGroup,
  selectSceneSelected,
} from '@containers/Home/selectors';
import { I_Scene } from '@containers/Home/types';
import React, {
  Ref,
  useRef,
  useMemo,
  useEffect,
  forwardRef,
  useCallback,
  useImperativeHandle,
  useState,
} from 'react';
import { useSelector } from 'react-redux';
import { Tabs } from 'antd';
import ListSceneFlickity from './FlickityScenes';
import { FlickityStyle } from '@styles/flickity';
import { ListSceneWrapper } from './styled';
import 'flickity/css/flickity.css';

interface Props {
  isShow: boolean;
  className?: string;
  onChangeScene: (scene: I_Scene) => void;
}

export type Type_RefFuncSlide = {
  onPrevScene: () => void;
  onNextScene: () => void;
};

const ListScene = forwardRef(
  (
    { onChangeScene, className, isShow }: Props,
    ref: Ref<Type_RefFuncSlide>,
  ) => {
    const flickityRef = useRef(null!);

    const [isShowFlickity, setIsShowFlickity] = useState(false);

    const listGroup = useSelector(selectListGroup);
    const sceneSelected = useSelector(selectSceneSelected);

    const groupActive = useMemo(
      () =>
        listGroup.length === 1 && listGroup[0].id === 'uncategory'
          ? 'uncategory'
          : listGroup.filter(item => sceneSelected?.group === item.id)[0]?.id ||
            '',
      [listGroup, sceneSelected],
    );

    const scenesGroupActive = useMemo(
      () => listGroup.filter(item => item.id === groupActive)[0]?.scenes || [],
      [groupActive, listGroup],
    );

    const currentIndex = useMemo(
      () => scenesGroupActive.findIndex(item => item.id === sceneSelected?.id),
      [sceneSelected, scenesGroupActive],
    );

    const onClickScene = useCallback(
      (_event, _pointer, cellElement, cellIndex) => {
        if (!cellElement) return;
        onChangeScene(scenesGroupActive[cellIndex]);
      },
      [onChangeScene, scenesGroupActive],
    );

    useImperativeHandle(ref, () => ({
      onPrevScene() {
        if (currentIndex !== 0) {
          const index = currentIndex - 1;
          onChangeScene(scenesGroupActive[index]);
          flickityRef.current?.select(index, false);
        }
      },
      onNextScene() {
        if (currentIndex + 1 !== scenesGroupActive.length) {
          const index = currentIndex + 1;
          onChangeScene(scenesGroupActive[index]);
          flickityRef.current?.select(index, false);
        }
      },
    }));

    useEffect(() => {
      setIsShowFlickity(true);
    }, []);

    useEffect(() => {
      if (flickityRef?.current)
        flickityRef.current?.on('staticClick', onClickScene);
      return () => {
        if (flickityRef?.current)
          flickityRef.current?.off('staticClick', onClickScene);
      };
    }, [groupActive, onClickScene, isShowFlickity]);

    return (
      <ListSceneWrapper
        className={`${className} ${isShow ? '' : '--hide'} ${
          listGroup.length === 1 && listGroup[0].id === 'uncategory'
            ? 'hideNav'
            : ''
        }`}
      >
        {listGroup.length === 1 && listGroup[0].id === 'uncategory' ? (
          <div className="scenes">
            {isShowFlickity && (
              <ListSceneFlickity
                sceneSelected={sceneSelected}
                scenesGroupActive={scenesGroupActive}
                onSetRef={c => {
                  if (!flickityRef.current && c) {
                    flickityRef.current = c;
                    flickityRef.current?.on('staticClick', onClickScene);
                  }
                }}
              />
            )}
          </div>
        ) : (
          <Tabs
            activeKey={groupActive}
            tabPosition="top"
            onTabClick={key => {
              const group = listGroup.filter(item => item.id === key)[0];
              if (group.scenes) {
                flickityRef.current = null;
                onChangeScene(group.scenes[0]);
              }
            }}
            className="--tabs"
          >
            {listGroup.map(group => (
              <Tabs.TabPane
                tab={`${group.name || 'N/A'}`}
                key={group.id}
                className={`itemSceneCate ${
                  groupActive === group.id ? 'active' : ''
                }`}
              >
                {groupActive === group.id && isShowFlickity && (
                  <ListSceneFlickity
                    sceneSelected={sceneSelected}
                    scenesGroupActive={scenesGroupActive}
                    onSetRef={c => {
                      if (!flickityRef.current) {
                        flickityRef.current = c;
                        flickityRef.current?.on('staticClick', onClickScene);
                      }
                    }}
                  />
                )}
              </Tabs.TabPane>
            ))}
          </Tabs>
        )}

        <FlickityStyle />
      </ListSceneWrapper>
    );
  },
);

export default ListScene;
